/*-------------- Basic styling --------------*/

body,
html {
  box-sizing: border-box;
  font-family: 'Merriweather';
}

*, *:before, *:after {
 	box-sizing: inherit;
}

#apps {
  display: flex;
  align-items: center;
  justify-content: center;
}

section#intro {
  margin-bottom: 10em;
}

section#intro p {
  font-size: 1.3em;
  width: 58%;
  line-height: 1.5;
  margin: 0 auto;
}

.app-title {
  font-family: 'Scada', cursive;
}



/*-------------- First App: Pomodoro --------------*/

.pomodoro-app {
  padding: 3em;
}

.pomodoro-info {
  margin-bottom: 2em;
}

.pomodoro-info h2 {
  font-family: 'Scada', cursive;
}

.pomodoro-info p {
  font-size: 1em;
}

.pomodoro-main {
  color: #b84545;
}

.heading {
  display: block;
  width: 50%;
  margin: 0 auto;
  background: rgba(99, 99, 29, 0.57);
}

/*.main-title {
  text-transform: uppercase;
  text-align: center;
  font-size: 300px;
  margin-bottom: 35px;
  color: #fff;
}*/

.measurement {
  font-size: .9em;
  text-transform: lowercase;
}

.break-title {
  text-transform: uppercase;
  text-align: right;
  margin-right: 19%;
  font-size: .95em;
}

.timer-title {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.4em;
  font-size: 1.8em;
  margin-bottom: 0;
}

.session-title {
  text-transform: uppercase;
  text-align: left;
  font-size: .95em;
  margin-left: 18%;
}

#breakLength,
#sessionLength {
  font-size: 2.4em;
}

.pomodoro-btn {
  font-size: 1.2em;
  margin-right: .3em;
}

.btnSide {
  background-color: #ffffff;
  border: 1px solid #9a7d54;
  border-radius: 5px;
}

.btnSide:hover {
  background-color: #9a7d54;
  color: #ffffff;
}

#pomodoro {
  text-align: center;
  font-size: 4em;
  border: 1px solid #9a7d54;
  border-radius: 3.4em;
  line-height: 1.5em;
  margin-bottom: .3em;
}

.button-middle {
  background-color: #ffffff;
  border: 1px solid #9a7d54;
  border-radius: 3.125em;
}

.button-middle:hover {
  background-color: #9a7d54;
  color: #fff;
}

.js-app {
  width: auto;
  height: auto;
  background: url(./img/cream_pixels.png);
}

/*------------- Third App: Wikipedia Viewer  ----------*/

a {
  text-decoration: none;
  color: #E91E63;
}
a:hover {
  color: #EC407A;
}

#wiki {
  width: 52em;
  padding: 2em;
  margin: 7em auto;
  text-align: center;
  background: url(./img/seigaiha.png)
}

.search-box-container {
  margin-bottom: 10px;
  margin-top: 1.7em;
}

.search-box-text {
  background-color: transparent;
  padding: 10px;
  width: 90%;
  max-width: 300px;
  border: 1px solid #000000;
  color: #000000;
  text-align: center;
  font-size: 1.3em;
  font-weight: 300;
}
.search-box-text:focus {
  border-color: #E91E63;
  outline: none;
}

.search-box-submit {
  border: 1px solid #000000;
  background-color: #000000;
  cursor: pointer;
  padding: 10px;
}
.search-box-submit:active {
  background-color: #a3a3a3;
}
.search-box-submit:focus, .search-box-submitactive {
  outline: none;
}

.random-text {
  color: #000000;
}

.result-list {
  width: 100%;
}

.single-result {
  color: #333;
  text-decoration: none;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  display: block;
  background-color: #f4f4f4;
  padding: 20px;
  margin-bottom: 5px;
  text-align: left;
  border: 3px solid #b3a7a7;
  transition: 0.1s ease-out all;
}
.single-result h3 {
  font-size: 1.2em;
  margin-bottom: 0.3em;
}
.single-result p {
  font-size: 1em;
}
.single-result:hover {
  color: #333;
  border-color: #E91E63;
}

/*------------- Fourth App: Calculator ----------*/

.calculator-key {
  display: block;
  background: none;
  border: none;
  padding: 0;
  font-family: inherit;
  user-select: none;
  cursor: pointer;
  outline: none;

  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

button:active {
  box-shadow: inset 0 0 80px 0 rgba(148, 157, 156, 0.25);
}

#calc-wrapper {
  /*height: 100vh;*/
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(./img/geometry2.png);
  /*width: 23em;
  margin: 7em auto;
  background: url(./img/geometry2.png);*/
}

#calc-app {
  width: 24em;
  height: 38em;
  position: relative;
  margin: 2em auto;
  /*width: 23em;
  height: 38em;
  margin: 0 auto;
  position: relative;
  background: url(./img/school.png);*/
}

/* Apply horizontal centering to Wiki & Calculator*/
#align-center {
  width: 70%;
  margin: 0 auto;
  position: relative;
}

#calculator {
  width: 22.9em;
  height: 100%;
  background: #a07e7e;
  margin: 4em auto;
  display: flex;
  flex-direction: column;
  box-shadow: 0 10px 6px -6px #d2cccc;
}

#wrapper #calculator {
  box-shadow: 0 0 20px 0 #aaa;
}

.calculator-display {
  color: white;
  background: #304c71;
  line-height: 130px;
  font-size: 6em;
  flex: 1;
}

.auto-scaling-text {
  display: inline-block;
}

.calculator-display .auto-scaling-text {
  padding: 0 30px;
  right: 0;
  position: absolute;
  transform-origin: right;
}

.calculator-keypad {
  height: 400px;

  display: flex;
}

#calculator .input-keys {
  width: 240px;
}

#calculator .function-keys {
  display: flex;
}

#calculator .digit-keys {
  background: #a4f7ec;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
}

.calculator-key {
  width: 80px;
  height: 80px;
  border-top: 1px solid #777;
  border-right: 1px solid #666;
  text-align: center;
  line-height: 80px;
}
#calculator .function-keys .calculator-key {
  font-size: 2em;
}
#calculator .function-keys .key-multiply {
  line-height: 50px;
}
#calculator .digit-keys .calculator-key {
  font-size: 2.25em;
}
#calculator .digit-keys .key-0 {
  width: 160px;
  text-align: left;
  padding-left: 32px;
}
#calculator .digit-keys .key-dot {
  padding-top: 1em;
  font-size: 0.75em;
}
#calculator .operator-keys .calculator-key {
  color: white;
  border-right: 0;
  font-size: 3em;
}

#calculator .function-keys {
  background: #d7f7d2;
}
#calculator .operator-keys {
  background:  #b7d058;
}





