.button-bt {
   background-color: white;
   color: black;
   transform: perspective(1px) translateZ(0);
   box-shadow: 0 0 1px transparent;
   transition-duration: 0.3s;
   transition-property: background;
   /*box-shadow: inset 0 0 0 4px #e1e1e1, 0 0 1px transparent;*/
}



.button-bt-default {
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
  position: relative;
}
.button-bt-default:before {
  content: '';
  position: absolute;
  border: white solid 1px;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  opacity: 0;
  transition-duration: 0.3s;
  transition-property: opacity;
}
.button-bt-default:hover:before, .button-bt-default:focus:before, .button-bt-default:active:before {
  opacity: 1;
}

.button-bt-primary {
   background-color: #8c8996;
   color: white;
}

.nav-pills>li+li {
    margin-left: 6px;
}


.pull-right {
   outline: none;
}

.nav>li>a.button-bt-primary:hover, .nav>li>a.button-bt-primary:focus {
   background-color: #908aa3;
   color: white;
}

.button-bt-default {
   background-color: #8ed2a8;
   color: white;
}


.nav>li>a.button-bt-default:hover, .nav>li>a.button-bt-default:focus {
   background-color: #aaba9b;
   color: white;
}

