/* ----------- Responsiveness ------------- */

@media (min-width: 300px) and (max-width: 769px) {

  /* Pomodoro */
  .col-sm-4 {
    width: 100%;
    float: left;
    margin-bottom: 3em;
  }

  .timer-text {
    display: none;
  }

  .col-sm-6.text-center:before {
    content: 'Робоча сесія';
    font-size: 1.2em;
  }

  .col-sm-6.text-center:nth-child(2):before {
    content: 'Перерва';
    font-size: 1.2em;
  }

  /* Todo */
  .todo-wrapper {
    width: 85%;
    margin: 3em auto;
  }

  #todo {
    width: 35em;
  }

  #todo .input-group{
    width: 33em;
  }

  .alert {
    width: 33em;
    height: 4em;
  }

  .left_count {
    float: none;
    width: 50%;
    margin: 0 auto;
  }

  .left_count .label {
    margin-bottom: 1.5em;
  }

  /* Wikipedia Viewer */

  #wiki {
    width: 80%;
  }

  input.form-control::-webkit-input-placeholder::before {
    content: 'wow, how cool';
  }

}

@media screen and (max-width: 605px) {

  /* Intro*/

  section#intro h2 {
    font-size: 2.2em;
  }

  section#intro p {
    width: auto;
    font-size: 1.2em;
  }

  /* Todo */

  .todo-wrapper {
    display: block;
  }

  #todo {
    width: 28em;
  }

  #todo .input-group{
    width: 26em;
  }

  .alert {
    width: 26em;
    height: 4em;
  }

}

@media screen and (max-width: 485px) {

  /* General */

  .app-title {
    font-size: 2em;
  }

  /* Todo */

  #todo {
    width: 23em;
  }

  #todo .input-group{
    width: 21em;
  }

  .alert {
    width: 21em;
    height: 4em;
  }

  button.btn.check_btn {
    font-size: 13px;
  }

  .list-group-item > span {
    font-size: 1.1em;
  }

  .left_count {
    float: none;
    width: 60%;
    margin: 0 auto;
  }

  .clear_completed {
    float: none;
    margin-top: 1.7em;
  }

}


@media screen and (max-width: 400px) {

  /* Todo */

  #todo {
    width: 21em;
  }

  #todo .input-group{
    width: 19em;
  }

  .alert {
    width: 19em;
    height: 5.3em;
  }

  .left_count {
    width: 70%;
  }

}

@media screen and (max-width: 360px) {

  /* Todo */

  #todo {
    width: 20em;
  }

  #todo .input-group{
    width: 18em;
  }

  .alert {
    width: 18em;
    height: 5.3em;
  }

